<p><span class="rvts26">主属性</span></p>
</td>
<td width="122" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">主派系</span></p>
</td>
<td width="196" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">默认技能</span><span class="rvts27">1</span></p>
</td>
<td width="196" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">默认技能</span><span class="rvts27">2</span></p>
</td>
<td width="196" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">默认技能</span><span class="rvts27">3</span></p>
</td>
</tr>
<tr valign="top">
<td width="200" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">灵巧，力量</span></p>
</td>
<td width="122" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">变形</span></p>
</td>
<td width="196" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber92.jpg"><span class="rvts39"></span>
